@charset "UTF-8";

*{
    margin: 0px; padding: 0px;
}
.title{
    text-align: center;
    padding: 30px 0px;
}
ul,ol,li{
    list-style: none;
}
.product{
    display: flex;
    justify-content: space-around;
    width: 1200px;
    margin: 0px auto;
}
.product> li{
    width: 300px;
    padding : 0px 10px;
}
.product > li img{
    width: 100%;
    transition: 0.5s;
}
.product >li img:hover{
    scale: 1.2;
    filter: brightness(0.8);
}

.price{
    color: #f00;
    font-size: 18px;
    padding: 15px 0px;
    font-weight: bold;
}
.review{
    font-weight: normal;
}
.product >li > p:first-of-type{
    position: relative;
    overflow: hidden;
}
.product .cart{
    position: absolute;
    bottom: 20px;
    right: 25px;
    font-size: 24px;
    padding: 20px 20px;
    background: rgb(95, 0, 128);
    color: #fff;
    border-radius: 50%;
    transition: 0.5s;
    display: none;
}
.product >li:hover .cart{
    display: block;
}